import React, { PureComponent } from "react";
import CtaButton from "../../Elements/CtaButton";
import { RATINGS_PATH } from '../../../../constants'

export default class CardHover extends PureComponent {
  render() {   
    const {item, wall, network} = this.props;
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0 && item.hotspot === 0) ? true : false; 
    const nerworkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor;
    const styleCta ={fontFamily:wall.ThemeRule.css_font}; const networkIcon = network.icon.replace('fa-', '');
    return (
      <div className="ts_sp_Post_hover">
        <div className="ts_sp_card_hover_in">
          <div className="ts_sp_card_hover_overlay" style={{ backgroundColor: 'rgba(0, 0, 0, 0.6)'}}><div></div></div>
          <div className="ts_sp_card_hover_content">
            <div className="ts_sp_slider-social-icon" style={{ fontSize: 24, color: 'rgb(0, 0, 0)'}}>
            {(item.rating != null && item.rating != 0) ? <div className="ts_sp_social_rating" style={{backgroundImage: `url(${RATINGS_PATH}${item.networkId}/${item.rating}.png)`}} ><div></div></div>
             : <div className={`tb__icon tb-${networkIcon}`} style={{ color: nerworkColor }}><div></div></div>
            }
            </div>
            <div className="ts_sp_card_shop_btn">
            {isShopIcon && <CtaButton shoppingText={wall.ThemeRule.shoppingText} ctaClass={'ts_sp_shop_btn'} styleCta={styleCta}/>}
            </div>
          </div>
        </div>
      </div>
    );
  }
}