import React, { PureComponent } from "react";
import reactImageSize from 'react-image-size';
import HotSpot from "../../Elements/HotSpot";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT, marginTop_mainDiv } from '../../../../utils'
export default class Image extends PureComponent {

  componentDidMount() {
    marginTop_mainDiv();
  }
  


  render() {
    const { ImageClass, ImageUrl, item, ugc_products, wall,height,width } = this.props
    const ImageBg = { backgroundImage: `url(${ImageUrl})` };
    const styleImg = { height: item.hotspot ? (width > height) ? `auto` : `100%`:(width > height) ? '100%':`auto`, width: item.hotspot ? (width > height) ? `100%`: `auto`:(width > height) ? 'auto':`100%`}

 
    return <>
            <div className="ts_sp_image_blur_bg" style={ImageBg}><div></div></div>
            <div className={ImageClass} id="suresh" style={styleImg}> 
            <img loading="lazy"  data-src={ImageUrl}  src={ImageUrl}  data-is-optmized="0" style={styleImg} data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} height={height} width={width} className={`imgID${item.id}`} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }} alt="image" />
            {item.hotspot ? <HotSpot ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} item={item} /> : null}
          </div>
          
      </>
    ;
  }
}