import React, { PureComponent } from "react";
import reactImageSize from 'react-image-size';
import Image from "./Image";
import {convertObjectToArray, ShopableText} from '../../../../utils' 
export default class SquarePhotoMedia extends PureComponent {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.state = {
      height: '100',
      width: '100',
    
    };
  }
  componentWillMount() {
    const { item } = this.props;
    if(item.mediaHeight && item.mediaWidth !=""){
      this.setState({
        height : (item.mediaHeight * 100) / item.mediaWidth,
         width : (item.mediaWidth * 100) / item.mediaHeight,
      })
      
    }
      else{
         
        reactImageSize(item.postFileNew).then(({ width, height }) => this.setState({
          height: (height * 100) / width,
          width: (width * 100) / height
        })).catch((errorMessage) =>
        this.setState({
          height: 100,
          width: 100,
          error: errorMessage
        }))
      // alert('ok')
      }
      }

  render() {    
    const { item , wall} = this.props;
    const { height , width} = this.state;

    const ugc_products = convertObjectToArray(item.ugc_products);

    return (
      <div className="tb_sp_media_wrap">
           {wall.User.id === 31665 ? <ShopableText ugc_products={item.ugc_products}  />:''}
         
          {width ?<Image  ImageClass={'tb_sp_image'} height={height} width={width} ImageUrl={item.postFileNew} ugc_products={ugc_products} item={item} wall={wall} />:''}
      </div>
    );
  }
}